.table_div{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 90vw;
    height: 90vh;

}

.table_heading{
   
    font-weight: bold;
}
.table_dates{
    margin: 0px 5px;
}
.table_date{

    font-weight: 600;
}
.table_date_select{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.table_settings{
    width: 90px;
    height: 30px;
    border: 1px solid lightgray;
    padding: 0px 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.table_add_remove{
    width: 90vw;
    margin-top: 3%;
    padding: 10px;
    border: 1px solid lightgray;
}
.table_btns{
    display: flex;
    justify-content: space-around;
}
.table_btn{
    width: 100px;
    height: 30px;
    background-color: blue;
    border: 1px solid lightgray;
    color: #fff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.table_info{
    padding: 25px;
    width: 90vw;
    
}
.table_data{
    width: 800px;
    margin: 0px auto;
    background-color: rgb(78, 157, 179);
    color: #fff;
    font-weight: 700;
}
.table_data tr td{
    text-align: center;
}
.date_info{
    display: flex;
}
.date_info h5{
    padding: 5px;
}